.portfolio__categories {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: 40px;
}

.portfolio__tag:not(:last-child) {
    margin-right: 10px;
}

.portfolio__tag {
    cursor: pointer;
    color: #222021;
    font-size: 18px;
    font-weight: 500;
    position: relative;
}

.portfolio__tag.active.portfolio__tag::after {
    width: 100%;
}

.portfolio__tag::after {
    content: '';
    display: block;
    position: absolute;
    bottom: 0;
    background-color: #222021;
    width: 0%;
    height: 1px;
    transition: width .3s ease-in-out;
}

@media (hover:hover) {
    .portfolio__tag:hover.portfolio__tag::after {
        width: 100%;
    }
}

.portfolio__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
}

.portfolio__item--img {
    width: 362px;
    height: 362px;
    margin-bottom: 40px;
    object-fit: cover;
    transition: all 1s ease-out;
}


.portfolio__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    overflow: hidden;
    max-height: 362px;
    max-width: 362px;
    position: relative;
}


@media (hover:hover) {
    .portfolio__item:hover .portfolio__item--img {
        transform: scale(1.21);
        opacity: 1;
        filter: brightness(65%)
    }

    .portfolio__item:hover .portfolio__item--name{
        opacity: 1;
        transform: translateY(-20px);
    }
}

.portfolio__item--link {
    position: absolute;
    width: 100%;
    height: 100%;
}


.portfolio__item--name {
    position: absolute;
    margin: 0;
    font-size: 32px;
    text-transform: uppercase;
    letter-spacing: 2px;
    text-align: center;
    font-weight: 500;
    color: #D6D7DD;
    user-select: none;
    bottom: 0px;
    opacity: 0;
    transition: all 1s ease-in-out;
}

@media (max-width:1200px) {
    .portfolio__list {
        grid-template-columns: repeat(2, 1fr);
        justify-items: center;
    }
}

@media (max-width:781px) {
    .portfolio__item--img {
        width: 300px;
        height: 300px;
        margin-bottom: 40px;
        object-fit: cover;
        transition: all 1s ease-out;
    }

    .portfolio__item {
        max-height: 300px;
        max-width: 300px;
    }
}

@media (max-width:662px) {
    .portfolio__list {
        grid-template-columns: repeat(1, 1fr);
        justify-items: center;
    }
}